#listView a.btn-quick-view {
    bottom: 20px;
}
.select2-results__message {
    display: none;
}
.select2-dropdown{
    border: none;
}
.discPer {
    position: relative;
    background: #ed0000;
    color: white!important;
    height: 18px;
    /*width: 27px;*/
    width: 23px;
    /*border-radius: 4px 0 0 4px;*/
    border-radius: 0 4px 4px 0;
    /*font-size: 15px;*/
    font-size: 12px !important;
    line-height: 19px;
    display: inline-block;
    /*float: left;*/
    float: right;
    text-align: center;
    /*margin-right: 13px;*/
}

.discPer:before {
    /*left: 100%;*/
    right: 100%;
    border: solid transparent;
    content: " ";
    position: absolute;
    pointer-events: none;
    border-color: rgba(237, 0, 0, 0);
    /*border-left-color: #ed0000;*/
    border-right-color: #ed0000;
    border-width: 9px;
}


#gridView{
    width: auto;
    display: inline-block;
}
#searchGrid{
    /*width: auto;*/
}
#listView{
    width: auto;
}
#listView > li .product-info{
    width: auto;
}
.filter-nav-c{
    min-width: 170px;
    margin: 0;
    padding: 2px;
}
#gridView > li ul li {
    vertical-align: top;
    float: right;
    font-size: 14px;
    margin: 0;
    text-align: center;
    min-width: 140px;
    border-bottom: 1px solid #cac9c9;
    padding: 0 10px;
}
/*#gridView ul.formats > li {*/
/*border: none!important;*/
/*}*/
#searchGrid ul.search-utility li.pagination{
    float: right;
}
@media screen and (min-width:767px){
    .narrow-results{display:block}
}
@media screen and (max-width:766px){
    .format--content {
        text-indent: 110px!important;
        margin-top: -20px;
    }
    #searchGrid ul.search-utility li.pagination > ul{
        display: inline-block;
    }
    #gridView > li ul{
        text-align: center;
    }
    #gridView > li ul li{
        float: none;
        display: inline-block;
    }
    #searchGrid ul.search-utility li.pagination > ul > li {
        display: inline-block;
        float: none;
    }
    #searchGrid ul.search-utility li.pagination > ul{
        text-align: center;
    }
    .narrow-results{display:none}
    .sidenav {
        height: 100%;
        width: 0;
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        background-color: #111;
        overflow-x: hidden;
        transition: 0.5s;
        padding-top: 60px;
        text-align:center;
    }

    .sidenav a {
        padding: 8px 8px 8px 32px;
        text-decoration: none;
        /*font-size: 25px;*/
        color: #818181;
        display: block;
        transition: 0.3s;

    }

    .sidenav a:hover{
        color: #f1f1f1;
    }

    .sidenav .closebtn {
        /*position: absolute; */
        /* top: 0; */
        right: 66px;
        font-size: 22px;
        margin-left: 0px;
    }

    #sortCat{
        background: transparent;
        width: 21px;
        padding: 30px 0 0 0;
        height: 32px;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background: url('/assets/img/new/order.JPG') 0% no-repeat #fff;
        background-size: 66%;
    }
}

.tooltip-inner {
    width:250px !important;
    max-width: 250px !important;
}
.glyphicon {
    font-family: 'Glyphicons Halflings' !important;
}
.discPer-Pdocuct {
    position: relative;
    background: #ed0000;
    color: white;
    height: 24px;
    width: 30px;
    border-radius: 4px 0 0 4px;
    font-size: 15px;
    line-height: 25px;
    display: inline-block;
    float: left;
    text-align: center;
}
.discPer-Pdocuct:after {
    left: 100%;
    border: solid transparent;
    content: " ";
    position: absolute;
    pointer-events: none;
    border-color: rgba(237, 0, 0, 0);
    border-left-color: #ed0000;
    border-width: 12px;
}