#refinements {
  background: #fff;
  border: none;
  border-right: 1px solid #ebeaea;
  height: inherit;
  min-height: 540px;
  padding: 0 0px 0 5px;
  position: relative;
}
#refinements h3 {
  color: #fdb816;
  font-family: Milo;
  font-style: italic;
  font-size: 16px;
  margin: 0;
  padding-bottom: 5px;
  direction: rtl;
}
#refinements h3.left-nav-header {
  border-top: 1px solid #b2a58e;
  padding-top: 25px;
}
#refinements:before {
  box-shadow: -1px 13px 4px 6px rgba(213, 210, 210, 0.3);
  content: "";
  height: 90%;
  right: 0;
  position: absolute;
  z-index: -1;
}
 > #refinements:first-child {
  margin-top: 0;
}
#refinements ul.selected-facets {
  position: relative;
  border: 1px solid #b2b0aa;
  overflow: auto;
  /*max-height: 500px;*/
}
#refinements ul.selected-facets li {
  /*background: #fafaf6;*/
  /*border-bottom: 1px solid #d2d2d5;*/
  padding: 0;
  position: relative;
  direction: rtl;
  text-align: right;
  /*color: #0B3750;*/
  font-family: Milo;
  padding: 0 4px;
}
#refinements ul.selected-facets li a span {
  text-align: right;
  color: #7b7b7b;
  font-family: Milo;
  font-size: 13px;
}
#refinements ul.selected-facets li:hover {
  background: #f1f0e8;
}
#refinements ul.selected-facets li:hover a span {
  color: #fbbe0d !important;
}
#refinements ul.selected-facets li:first-child {
  /*border-top: 1px solid #b2a58e;*/
}
#refinements ul.selected-facets li > a {
  color: #000;
  display: inline-block;
  font-family: Milo;
  font-size: 15px;
  padding: 3px 3px 3px 0;
  text-decoration: none;
  width: 85%;
  direction: rtl;
  text-align: right;
}
#refinements ul.selected-facets li > a:hover {
  color: #3169a8;

}
#refinements ul.selected-facets li > a:hover:after {
  color: #3169a8;
}
#refinements ul.selected-facets li > a:after {
  color: #b8b6b2;
  /*content: "+";*/
  display: inline-block;
  font-family: Arial;
  font-size: 24px;
  height: 30px;
  position: absolute;
  left: 0;
  top: 10px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  width: 30px;
}
#refinements ul.selected-facets li > a:active:after {
  background-position: -33px -37px;
}
#refinements dl {
  margin: 0;
  /*overflow-x: hidden;*/
  max-height: 570px;
  /*min-height: 30px;*/
}
#refinements dl dd {
  margin: 0;
  padding: 5px 8px 5px 0;
  direction: rtl;
}
#refinements dl dd a {
  color: #000;
}
#refinements dl dd a:hover {
  color: #3169a8;
}
#refinements dl dd a.show-more-link {
  font-weight: 700;
}
#refinements dl dd a.show-more {
  font-weight: 700;
}
#refinements dl dt {
  margin: 0;
  border-top: 1px solid #b2a58e;
  cursor: pointer;
  font-family: Milo;
  font-size: 14px;
  font-weight: 400;
  padding: 8px 8px 5px 0;
  position: relative;
  background: #e2e2e2;
  margin-bottom: 5px;
  margin-top: 5px;
  direction: rtl;
  border-radius: 3px;
}
#refinements dl dd.viewMoreviewLessToggle a {
  font-weight: 700;
  padding-left: 0;
}
#refinements > dl {
  margin-top: 10px;
  padding-bottom: 10px;
}
#refinements .refinement:last-of-type {
  /*border-bottom: 1px solid #0b3750;*/
}
#refinements dt:after {
  color: #0B3750;
  content: "-";
  font-size: 34px;
  font-weight: 400;
  line-height: 20px;
  position: absolute;
  overflow: hidden;
  left: 6px;
  top: 1px;
}
#refinements dt.augHeader {
  cursor: default;
}
#refinements dt.augHeader:after {
  content: "";
}
#refinements .facetCollapsed dt:after {
  content: "+";
  font-size: 22px;
  /*top: 5px;*/
}
#refinements dl.checkbox-container dd a {
  display: block;
  position: relative;
}
#refinements dl.checkbox-container dd.viewMoreviewLessToggle a:before {
  background: 0 0;
}
